.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.custom-search-box-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  margin-top: 50px;
}

.custom-search-box-container .ais-SearchBox-input {
  width: 400px;
  font-size: 24px;
}

.custom-search-box-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  margin-top: 50px; 
}

.custom-search-box-container .ais-SearchBox-input {
  width: 400px;
  font-size: 24px;
}

.ais-Hits-list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr)); /* Adjust the minmax values as needed */
  grid-gap: 20px; /* Adjust the gap between grid items as needed */
  margin: 0 auto; /* Center the grid within its container */
  max-width: 1200px; /* Optional: Set a maximum width for the grid */
}

.ais-Hits-list a {
  color: #61dafb;
  text-decoration: none;
  margin-top: 10px;
  display: block;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}